.auth-page {
    background-color: #E4D6DA;
    background: linear-gradient(to right top, #D180AC, #BD99A2, #8F788D);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
}

.auth-container {
    background-color: #fff;
    border-radius: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
    width: 768px;
    max-width: 100%;
    min-height: 480px;
}

.auth-container p {
    font-size: 1rem;
    line-height: 1.75rem;
    letter-spacing: 0.3px;
    margin: 1.5rem 0;
    color: #8F788D;
}

.auth-container span {
    font-size: 12px;
    color: #8F788D;
}

.auth-container a {
    color: #8F788D;
    font-size: 13px;
    text-decoration: none;
    margin: 15px 0 10px;
}

.auth-container button {
    background-color: #D180AC;
    color: #fff;
    font-size: 12px;
    padding: 10px 45px;
    border: 1px solid transparent;
    border-radius: 10px;
    font-weight: 600;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    margin-top: 10px;
    cursor: pointer;
}

.auth-container button.hidden-btn {
    background-color: transparent;
    border-color: #fff;
}

.auth-container form {
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    height: 100%;
}

.auth-container input {
    background-color: #E4D6DA;
    border: none;
    margin: 8px 0;
    padding: 10px 15px;
    font-size: 13px;
    border-radius: 10px;
    width: 100%;
    outline: none;
    color: #8F788D;
}

.auth-form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
}

.sign-in {
    left: 0;
    width: 50%;
    z-index: 2;
}

.auth-container.active .sign-in {
    transform: translateX(100%);
}

.sign-up {
    left: 0;
    width: 50%;
    opacity: 0;
    z-index: 1;
}

.auth-container.active .sign-up {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: move 0.6s;
}

@keyframes move {
    0%, 49.99% {
        opacity: 0;
        z-index: 1;
    }
    50%, 100% {
        opacity: 1;
        z-index: 5;
    }
}

.toggle-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: all 0.6s ease-in-out;
    border-radius: 100px 0 0 50px;
    z-index: 1000;
}

.auth-container.active .toggle-container {
    transform: translateX(-100%);
    border-radius: 0 100px 50px 0;
}

.toggle {
    background-color: #8F788D;
    height: 100%;
    background: linear-gradient(to right, #D180AC, #BD99A2);
    color: #fff;
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
    transition: all 0.6s ease-in-out;
}

.toggle p {
    width: 90%;
    color: #fff;
}

.auth-container.active .toggle {
    transform: translateX(50%);
}

.toggle-panel {
    position: absolute;
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 30px;
    text-align: center;
    top: 0;
    transform: translateX(0);
    transition: all 0.6s ease-in-out;
}

.toggle-left {
    right: 45%;
    transform: translate(-200%);
}

.auth-container.active .toggle-left {
    transform: translateX(0);
}

.toggle-right {
    left: 45%;
    transform: translateX(0);
}

.auth-container.active .toggle-right {
    transform: translateX(200%);
}
