
.edit-page {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
}

.edit-popup {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    width: 1200px;
    height: 800px;
    background-color: #E4D6DA;
    border-radius: 4rem;
}

.edit-icons {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.edit-icons img {
    width: 44px;
    margin: 22px 22px 0;
    cursor: pointer;
}

.edit-popup h1 {
    font-size: 3rem;
    border-bottom: 2px solid #8F788D;
}

.form-container {
    width: 80%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 1rem;
}

.form-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
    max-height: 200px;
}

.form-group .form-label {
    font-size: 1.75rem;
    padding: 0 0 0.5rem 0.5rem;
}

.form-group input {
    width: 80%;
    height: 50px;
    border-radius: 1.25rem;
    padding-left: 0.75rem;
    font-size: 1.5rem;
    border: none;
    outline: none;
}

.form-group textarea {
    width: 80%;
    height: 200px;
    font-size: 1.25rem;
    padding: 0.5rem;
    border: none;
    outline: none;
    border-radius: 1rem;
}

.form-group img {
    width: 150px;
    margin: 0 calc(50% - 75px) 5rem calc(50% - 75px);
    cursor: pointer;
}

.form-container button {
    position: absolute;
    top: 680px;
    left: 500px;
    width: 200px;
    height: 80px;
    border-radius: 2rem;
    border: none;
    background-color: #D180AC;
    color: #FFF;
    font-size: 1.5rem;
    cursor: pointer;
}

.form-container button:hover {
    background-color: #8F788D;
}

.star-rating {
    display: flex;
    align-items: center;
    justify-content: center;
    scale: 2;
    margin-top: 0.5rem;
}



.hidden {
    display: none;
}

/* HTML: <div class="loader"></div> */
.loader {
    width: 60px;
    aspect-ratio: 2;
    --_g: no-repeat radial-gradient(circle closest-side,#000 90%,#0000);
    background: 
      var(--_g) 0%   50%,
      var(--_g) 50%  50%,
      var(--_g) 100% 50%;
    background-size: calc(100%/3) 50%;
    animation: l3 1.1s infinite linear;
    position: absolute;
    top: 55vh;
  }
  @keyframes l3 {
      20%{background-position:0%   0%, 50%  50%,100%  50%}
      40%{background-position:0% 100%, 50%   0%,100%  50%}
      60%{background-position:0%  50%, 50% 100%,100%   0%}
      80%{background-position:0%  50%, 50%  50%,100% 100%}
  }