
.home {
    background-color: #FFF;
    width: 100vw;
    height: 100vh;
    color: #000;
    display: flex;
    justify-content: center;
}

.content-container {
    display: flex;
    flex-direction: column;
    width: 80vw;
    max-height: 85vh;
    margin-top: 100px;
    background-color: #FFF;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-width: thin;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
}

.info-bar {
    display: grid;
    grid-template-columns: repeat(4, auto);
    width: 100%;
    min-height: 30px;
    background-color: #BD99A2;
    align-items: center;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}

.info-bar div {
    width: calc(80vw / 4);
    display: flex;
    align-items: center;
}

.seperator {
    margin-right: 1rem;
    border-right: 2px solid slategrey;
    height: 100%;
}

.info-name {
    margin-left: 1rem;
}

.info {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.info span {
    padding-right: 0.5rem;
    font-size: 1.5rem;
    cursor: pointer;
}

.score span {
    padding-right: 5rem;
}


.library-container {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.library-entry {
    display: grid;
    grid-template-columns: repeat(4, auto);
    width: calc(100% - 1rem);
    height: 100px;
    background-color: #E4D6DA;
    align-items: center;
    padding-left: 1rem;
    border-top: 1px solid slategrey;
}

.library-entry:hover {
    background-color: #D180AC;
    color: #FFF;
}

.library-entry h3 {
    width: calc(80vw / 4);
    display: flex;
    align-items: center;
}

.library-entry img {
    height: 100px;
    margin-left: -1rem;
    margin-right: 1rem;
}

.home-rating {
    display: flex;
    align-items: center;
    justify-content: start;
    width: calc(80vw / 4);
}

