
.navbar {
    width: 100vw;
    height: 80px;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #8F788D;
}

.name {
    display: flex;
}

.name h1 {
    font-size: 2.5rem;
    color: #E4D6DA;
}

.name img {
    margin: 0 1.5rem;
    width: 50px;
}

.search {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-left: -10vw;
}

.search input {
    width: 600px;
    height: 45px;
    border-radius: 1rem;
    border: none;
    padding-left: 0.5rem;
    outline: none;
    font-size: 1.25rem;
}

.search-button img {
    width: 28px;
}

.add {
    margin: 0 1.5rem 0 -1.5rem;
}

.add img {
    width: 28px;
}

.button {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #D180AC;
    cursor: pointer;
}

.button:hover {
    background-color: #BD99A2;
}

.button-div {
    display: flex;
    margin-right: 3rem;
}

.logout {
    width: 100px;
    border-radius: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #D180AC;
    cursor: pointer;
}

.logout:hover {
    background-color: #BD99A2;
}