
.add-page {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
}

.add-popup {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    width: 1200px;
    height: 800px;
    background-color: #E4D6DA;
    border-radius: 4rem;
}

.add-popup img {
    width: 44px;
    margin: 20px 0 0 91%;
    cursor: pointer;
}

.add-popup h1 {
    font-size: 3rem;
    border-bottom: 2px solid #8F788D;
}

.form-container {
    width: 80%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 1rem;
}

.form-column {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.form-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
    max-height: 200px;
}

.form-group .form-label {
    font-size: 1.75rem;
    margin: 0 0 0.5rem 0.5rem;
}

.form-group input {
    width: 80%;
    height: 50px;
    border-radius: 1.25rem;
    padding-left: 0.75rem;
    font-size: 1.5rem;
    border: none;
    outline: none;
}

.form-group textarea {
    width: 80%;
    height: 200px;
    font-size: 1.25rem;
    padding: 0.5rem;
    border: none;
    outline: none;
    border-radius: 1rem;
}

.form-group img {
    width: 150px;
    margin: 0 calc(50% - 75px) 0 calc(50% - 75px);
    cursor: pointer;
    transition: transform ease-in-out 0.3s;
}

.form-group img:hover {
    transform: scale(1.1);
}


.form-container button {
    position: absolute;
    top: 680px;
    left: 500px;
    width: 200px;
    height: 80px;
    border-radius: 2rem;
    border: none;
    background-color: #D180AC;
    color: #FFF;
    font-size: 1.5rem;
}

.form-container button:hover {
    background-color: #8F788D;
}

.star-rating {
    display: flex;
    align-items: center;
    justify-content: center;
    scale: 2;
    margin-top: 0.5rem;
}

.change-cover {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    width: 1200px;
    height: 800px;
    background-color: #E4D6DA;
    border-radius: 4rem;
    z-index: 1;
}

.change-cover .close {
    width: 44px;
    margin: 20px 0 0 91%;
    cursor: pointer;
}

.change-cover .cover-container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 1rem;
    cursor: pointer;
    height: auto;
    overflow: auto;
    margin-bottom: 1rem;
    scrollbar-width: thin;
}

.cover-container img {
    margin: 1rem;
    transition: transform ease-in-out 0.3s;
}

.cover-container img:hover {
    transform: scale(1.2);
}

.cover-container p {
    font-size: 2rem;
    text-align: center;
    margin: 5rem 215%;
    width: 200px;
}

.auto-add {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    margin: 4rem 0;
}

.auto-add label {
    font-size: 1.75rem;
    margin: 0 0 1rem 0;
}

.auto-add input {
    width: 60%;
    height: 50px;
    border-radius: 1.25rem;
    padding-left: 0.75rem;
    font-size: 1.5rem;
    border: none;
    outline: none;
}

.auto-add .search-button {
    position: absolute;
    top: 370px;
    left: 500px;
    width: 200px;
    height: 80px;
    border-radius: 2rem;
    border: none;
    background-color: #D180AC;
    color: #FFF;
    font-size: 1.5rem;
    cursor: pointer;
}

.auto-add .search-button:hover {
    background-color: #8F788D;
}

.auto-add .manual-button {
    position: absolute;
    top: 680px;
    left: 500px;
    width: 200px;
    height: 80px;
    border-radius: 2rem;
    border: none;
    background-color: slategrey;
    color: #FFF;
    font-size: 1.5rem;
    cursor: pointer;
}

.auto-add .manual-button:hover {
    background-color: darkslategrey;
}



.form-group .hidden {
    display: none;
}

.change-cover .hidden {
    display: none;
}